import React from "react";
import workImages from "./imageData";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./portfolio-slider.css";

const PortfolioSlider = () => {
  const settings = {
    dots: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    multipleRow: true,
    speed: 500,
    rows: 1,
    centerMode: true,
    prevArrow: 0,
    responsive: [
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          centerMode: true,
          dots: true,
        },
      },
    ],
  };
  return (
    <Slider {...settings}>
      {workImages.map((images) => (
        <div key={images.id} className="cards">
          <img className="images" src={images.image} alt="card1" />
        </div>
      ))}
    </Slider>
  );
};

export default PortfolioSlider;
