import React, { useState } from "react";
import { HiOutlineMenuAlt3 } from "react-icons/hi";
import { RiCloseLine } from "react-icons/ri";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
// import { Link } from "react-router-dom";
import "./navbar.css";

const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);
  return (
    <div id="navbar" className="navbar_main">
      <div className="nav_container">
        <div className="lab_navbar_logo">
          <img id="brand_logo" src="logo.png" alt="logolab" />
        </div>
        <div className="nav_items">
          <a className="current_page" id="home" href="/">
            Home
          </a>
          <a href="#pricing">Pricing</a>
          {/* <Link to="/about">About Us</Link> */}
          <a className="current_page" href="#portfolios">
            Our Works
          </a>
        </div>
        <div className="whatsapp_btn_container">
          <a
            href="https://wa.me/9765978888"
            className="main_btn"
            target="_blank"
            rel="noopener noreferrer"
          >
            <WhatsAppIcon className="whatsapp_icon" />
            WhatsApp Us
          </a>
        </div>
        <div className="lab_navbar_menu">
          {toggleMenu ? (
            <RiCloseLine
              color="white"
              size={27}
              onClick={() => setToggleMenu(false)}
            />
          ) : (
            <HiOutlineMenuAlt3
              color="white"
              size={27}
              onClick={() => setToggleMenu(true)}
            />
          )}
          {toggleMenu && (
            <>
              <div className="lab_menu_container">
                <div className="drag1"></div>
                <div className="lab_menu_links">
                  <a id="home" href="/">
                    Home
                  </a>
                  <a href="#pricing">Pricing</a>
                  {/* <Link to="/about">About Us</Link> */}
                  <a href="#portfolios">Our Works</a>
                </div>
              </div>
            </>
          )}
        </div>
      </div>{" "}
    </div>
  );
};

export default Navbar;
