import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import { AiFillStar } from "react-icons/ai";
import "./clients.css";
import clientData from "./clientData";

const Clients = () => {
    useEffect(() => {
      Aos.init();
      Aos.refresh();
    }, []);
  return (
    <div className="main_client">
      <h1 className="clients_title" data-aos="fade-up" data-aos-duration="1000">
        What our clients say?
      </h1>
      {/*********************** Main Heading area *************************/}
      <div
        className="clients_header"
        data-aos="fade-up"
        data-aos-duration="1000"
      >
        <div className="review_left">
          <p>Logolab Nepal</p>
          <div className="ratings">
            <div className="rating">5.0</div>
            <div className="stars">
              <AiFillStar className="star_icon" />
              <AiFillStar className="star_icon" />
              <AiFillStar className="star_icon" />
              <AiFillStar className="star_icon" />
              <AiFillStar className="star_icon" />
            </div>
          </div>
          {/* <div className="goto_links">
            <a href="/" className="links">
              See all reviews
            </a>
            <a href="/" className="links">
              Write a review
            </a>
          </div> */}
        </div>
        <div className="review_right">
          <div>
            <img className="google_logo" src="google.png" alt="google_logo" />
          </div>
        </div>
      </div>
      <div className="clients_comm_area">
        {clientData.map((comment) => (
          <div
            key={comment.id}
            className="comm_box"
            data-aos="fade-up"
            data-aos-duration="1000"
          >
            <div className="box_head">
              <img className="google_review_images" src={comment.image} alt="clientimage" height={35} width={35} />
              <div className="name_stars">
                <h4>{comment.name}</h4>
                <div className="client_rating_stars">
                  <AiFillStar className="star_icon" />
                  <AiFillStar className="star_icon" />
                  <AiFillStar className="star_icon" />
                  <AiFillStar className="star_icon" />
                  <AiFillStar className="star_icon" />
                </div>
              </div>
            </div>
            <p className="clients_say">{comment.paragraph}</p>
          </div>
        ))}
      </div>
      {/************************ Heading Ends *********************************/}
    </div>
  );
};

export default Clients;
