import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import "./hero.css";

const Hero = () => {
  useEffect(() => {
    Aos.init();
    Aos.refresh();
  },[]);
  return (
    <div className="main_hero">
      <div className="hero_content_holder_container">
        <img className="golden_ratio" src="colorful.gif" alt="no_img" />
        <h1 className="content">
          DON’T JUST BUY A LOGO.
          <br />
          INVEST IN YOUR OWN BRAND.
        </h1>
        <div
          className="hero_btn_holder"
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          <button className="hero_btn">
            View Our Services
          </button>
        </div>
        <div className="scroll_animation">
          <span className="scroll_down"></span>
          <p>Scroll Down</p>
        </div>
      </div>
    </div>
  );
};

export default Hero;
