import React, { useEffect } from "react";
import Aos from "aos";
import "aos/dist/aos.css";
import YCarousel from "./YCarousel";
import "./your_customer.css";

const YourCustomer = () => {
  useEffect(() => {
    Aos.init();
    Aos.refresh();
  }, []);
  return (
    <div className="main_your_customer">
      <h1
        className="main_your_title"
        data-aos="fade-up"
        data-aos-duration="1000"
      >
        Your Customer
      </h1>
      <div
        className="customer_carousel"
        data-aos="fade-up"
        data-aos-duration="1000"
      >
        <YCarousel />
      </div>
    </div>
  );
};

export default YourCustomer;
