import React, { useEffect } from "react";
import PortfolioSlider from "./PortfolioSlider";
import workImages from "./imageData";
import Aos from "aos";
import "aos/dist/aos.css";
import "./works.css";

const Works = () => {
  useEffect(() => {
    Aos.init();
    Aos.refresh();
  }, []);
  return (
    <div id="portfolios" className="main_works">
      <h1 className="works_title" data-aos="fade-up" data-aos-duration="1000">
        Our Works
      </h1>
      <div
        className="card_container"
        data-aos="fade-up"
        data-aos-duration="1000"
      >
        {workImages.map((images) => (
          <div key={images.id} className="cards">
            <img
              className="images"
              src={images.image}
              alt="card1"
            />
          </div>
        ))}
      </div>
      <div className="responsive-slider">
        <PortfolioSlider />
      </div>
    </div>
  );
};

export default Works;
